<template>
    <div class="cartcontrol">
        <transition name="move">
        <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart">
            <span class=" inner icon-remove_circle_outline"></span>
        </div>
        </transition>
        <div class="cart-count" v-show="food.count>0">{{ food.count }}</div>
        <div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
    </div>
</template>

<style lang="sass">
.cartcontrol
    font-size: 0
    .cart-decrease
        display: inline-block
        padding: .375rem
        opacity: 1
        transform: translate3d(0,0,0)
        transition: all 0.4s linear
        .inner
            display: inline-block
            line-height: 1.5rem
            font-size: 1.5rem
            color: rgb(0,160,220)
            transition: all 0.4s linear
            transform: rotate(0)
        &.move-enter, &.move-leave-to
            opacity: 0
            transform: translate3d(1.5rem,0,0)
            .inner
                transform: rotate(180deg)
    .cart-count
        display: inline-block
        vertical-align: top
        width: .75rem
        padding-top: .375rem
        line-height: 1.5rem
        text-align: center
        font-size: .625rem
        color: rgb(147,153,159)
    .cart-add
        display: inline-block
        padding: .375rem
        line-height: 1.5rem
        font-size: 1.5rem
        color: rgb(0,160,220)
</style>

<script>
import Vue from 'vue';

export default {
    props: {
        food: {
            type: Object
        }
    },
    created() {
        console.log(this.food);
    },
    methods: {
        addCart(event) {
            if (!event._constructed) {
                return;
            }
            if (!this.food.count) {
                Vue.set(this.food, 'count', 1);
            }else{
                this.food.count++;
            }
            this.$emit('cart-add', event.target);
        },
        decreaseCart(event) {
            if (!event._constructed) {
                return;
            }
            if(this.food.count){
                this.food.count--;
            }
        },
    },
}
</script>
